.bdt {
    @media print {
        .bdt_slideshow { 
            .bdt_viewport img {
                display:none;
                &:first-child {
                    display:block !important;
                    opacity:1 !important;
                }
            }
            .bdt_prev, .bdt_next, .bdt-counter {
                display:none;
            }
        }
        * {
            font-size:8pt;
        }
        h1 {
            font-size:12pt;
        }
        h2 {
            font-size:10pt;
        }
        h3 {
            font-size:9pt;
        }
    }
}
/* DIVI theme */
@media print {
    .bdt_hide_in_print {
        display:none;
    }
    body {
        #et_mobile_nav_menu {
            display:none !important;
        }
    }
}