  .paging {
    margin:auto;
    display:block;
    text-align:center;
    padding:0;
    margin:0;
    
    li {
      display:inline-block;
      padding:0;
      margin:15px;
      a {
        display:inline-block;
        border-radius:50%;
        width:3em;
        height:3em;
        text-align: center;
        line-height: 3em;
        overflow: hidden;
        border:1px solid #ccc;

        &:not(.active) {
          transition: background-color .5s linear;
        }
        &:hover {
          background-color:#efefef;
        }
        &.active {
          background-color:theme-color("primary");
          color:#fff;
          font-weight:900;

          &:hover {
            cursor:default;
          }
        }
      }
    }
  }