  .vehicle_search {
    select, input {
      width: 100%;
      padding: 16px;
      border: none;
      -webkit-border-radius: 0;
      -moz-border-radius: 0;
      border-radius: 0;
      color: #999;
      background-color: #eee;
      font-size: 14px;
      -webkit-appearance: none;
    }
    .vehicleCategories, .makes {
      width:100%;
      background-color:#efefef;
      padding:16px;
      margin-bottom:16px; 
      
      a {
        width:10%;
        height:100px;
        display:inline-block;
        text-align:center;
        color:#333;
        text-decoration: none;
        margin-bottom: 16px;
      }
    }
    .makes a img {
      width:100%;
      height:auto;
    }
    button.search {
      display:block;
      width:100%;
    }
    .reset {
        background-color:transparent;
        color: theme-color("primary");
        border:none;
        padding:5px 10px;
    }
  }