.bdt_table {
    &.row {
        &>div {
            // border-bottom:1px solid #ccc;
            padding-bottom:$grid-gutter-width/3;
        }
    }
    .bdt_property {
        div:first-child {
            padding-right:0;
        }
        div:last-child {
            padding-left:10px;
            padding-top:10px;
            padding-bottom:10px;
        }
    }
}

.specificationsGroup {
    padding-bottom:$grid-gutter-width;

    h5 {
        padding-bottom:$grid-gutter-width/1.5;
    }
    .iconColumn {
        position:absolute;

        .bticon {
            font-size:2em;
        }
    }
    .contentColumn {
        position: relative;
        margin-left:3em; 
    }
}

.bdt_spec_label {
    font-size:.8em;
    color:theme-colors('gray-dark');
    padding: 5px 0 0 0 !important;
    line-height:1.4;
}
.bdt_spec_value {
    color:#000;
    padding: 0 0 $grid-gutter-width/3 0 !important;
}