$theme-colors: (
  primary: #555,
  secondary: $gray-600,
  success: $green,
  info: $cyan,
  warning: $yellow,
  danger: $red,
  light: $gray-100,
  dark: $gray-800
);
.bdt_color {
  color: theme-color("primary");
}
.bdt_bgcolor {
  background-color: theme-color("primary");
}
.bdt_bordercolor {
  border-color: theme-color("primary");
}
$slider-primary: theme-color("primary");
