.vehicleCard {
    background-color: #fcfcfc;
    margin-bottom:$grid-gutter-width;
    position:relative;
    overflow:hidden;

    a, a:hover {
        text-decoration: none;
    }
    .vehicleTitle {
        text-align: center;
        color:#000;
        font-size:1.1em;
        font-weight:600;
        padding:10px 15px;
        overflow:hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    .price {
        text-align: center;
        color:theme-color("primary");
        font-size:2em;
        font-weight:600;
        margin-bottom:1em;
    }
    .exVat {
        text-align: center;
        color:#ccc;
        font-size:.8em;
        line-height: 5px;
    }
    .priceLabel {
        text-align: center;
        color:#000;
        font-weight:700;
        font-size:.8em;
        position:absolute;
        left:0;
        right:0;
        margin-top:-2.4em;
    }
    .vehicleDescription {
        border:1px solid #eeeeee;
        border-top:0;
        position:relative;
    }
    img {
        width:100%;
        height:100%;
        object-fit: cover;
        position:absolute;
    }
    span:not(.row) {
        display:block;
    }

    ul {
        padding:15px;
        display:block;
    }
    .vehicleThumb {
        position:relative;
        padding-bottom:68%;
        height:0px;
        overflow:hidden;
    }
    .vehicleParamValue {
        font-size:1em;
        color:#000;
        text-align:center;
    }
    .vehicleParamLabel {
        font-size:.8em;
        color:#ccc;
        text-align:center;
    }
    .vehicleLabel {
        color:#fff;
        padding:3px $grid-gutter-width/2;
        position:absolute;
        left:0;
        top:20px;
        background-color:theme-color("gray");
        font-size:.7rem;
        text-transform: uppercase;
        font-weight:700;
        box-shadow:0 0 10px rgba(0,0,0,.15);

        &.new {
            background-color:theme-color("success");
        }
        &.sold {
            background-color:theme-color("danger");
        }
    }
}